/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  line-height: 1.4;
  position: relative;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
  margin: 0;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1, h2 {
  font-weight: 700;
}

h3, h4 {
  font-weight: 400;
}

.wrapper {
  width: 80%;
  margin: 0 auto;
}

.nav ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
  height: 8.1rem;
  align-items: center;
}
.nav a {
  color: white;
  text-decoration: none;
}

.red {
  background-color: red;
  min-height: 70vh;
}

.blue {
  background-color: blue;
}

.green {
  background-color: green;
  min-height: 42rem;
}